import { chain, mapKeys } from 'lodash';
import { VizProps } from '../common/interfaces/VizProps';

export interface MarkerGaugeProps extends VizProps {
    mode: string;
    width: number;
    height: number;
    value: string;
    orientation: string;
    majorUnit: number | string;
    showLabels: boolean;
    showValue: boolean;
    usePercentageRange: boolean;
    usePercentageValue: boolean;
    backgroundColor: string;
    ranges: any;
    options: any;
    fillColor: string;
    majorTickFillColor: string;
    majorTickStrokeColor: string;
    valueMarkerFillColor: string;
    valueMarkerLabelFillColor: string;
}

interface MarkerGaugeOptions {
    [key: string]: any;
}

// map for new options names to old option names
const markerGaugeOptionsMapping = {
    majorTickInterval: 'majorUnit',
    gaugeRanges: 'ranges',
};

/**
 * Helper method to update the option names to old option names
 * @method mapToOldKey
 * @param {Object} options
 * @returns {Object}
 */
export const mapToOldKey = (options: MarkerGaugeOptions): MarkerGaugeOptions =>
    mapKeys(options, (val, key) => (markerGaugeOptionsMapping[key] ? markerGaugeOptionsMapping[key] : key));

type LabelOrValue = 'label' | 'value';

/**
 * Helper method to map labelDisplay and valueDisplay options to pure component option names
 * @method mapToOldKey
 * @param {string} labelOrValue
 * @param {Object} options
 * @returns {Object}
 */
export const mapValueLabelDisplay = (
    labelOrValue: LabelOrValue,
    options: MarkerGaugeOptions
): MarkerGaugeOptions => {
    const rangeOrValue: string = labelOrValue === 'value' ? 'Value' : 'Range';
    const showKey: string = labelOrValue === 'value' ? 'showValue' : 'showLabels';
    const percentKey = `usePercentage${rangeOrValue}`;
    const optionString = `${labelOrValue}Display`;
    const newOptions = { ...options };
    if (newOptions[optionString]) {
        if (newOptions[optionString] === 'number') {
            newOptions[showKey] = true;
            newOptions[percentKey] = false;
        } else if (newOptions[optionString] === 'percentage') {
            newOptions[showKey] = true;
            newOptions[percentKey] = true;
        } else {
            newOptions[showKey] = false;
            newOptions[percentKey] = false;
        }
    }
    return newOptions;
};

/**
 * Chained helper for mapping the option properties (and values) we expose in config.ts into property values that marker gauge accepts
 * This is for options that has new key name and can not directly be mapped with SimpleXML or
 * don't have a 1-1 mapping in SimpleXML (i.e. a straightforward name mapping where values are preserved)
 * @method convertToMarkerGaugeProperties
 * @param {Object} originalOptions
 * @returns {Object}
 */
export const convertToMarkerGaugeProperties = (originalOptions: MarkerGaugeOptions): MarkerGaugeOptions =>
    chain(mapToOldKey(originalOptions))
        .thru(options => mapValueLabelDisplay('label', options))
        .thru(options => mapValueLabelDisplay('value', options))
        .value();
