import { chain } from 'lodash';
import ReactMarkerGauge from './PureMarkerGauge/MarkerGauge';
import config from './config';
import withDashboardViz from '../common/withDashboardViz';
import { convertToMarkerGaugeProperties } from './markerGaugeUtils';

export const PureMarkerGauge = ReactMarkerGauge;

const validOptions = Object.keys(config.optionsSchema);

export const computeVizProps: any = (props): Partial<any> => {
    const { dataSources, ...otherOptions } = props;

    const options = chain(otherOptions).pick(validOptions).thru(convertToMarkerGaugeProperties).value();

    const defaultColors = {
        fillColor: config.themes.defaultFillColor(props),
        majorTickFillColor: config.themes.majorTickFillColor(props),
        majorTickStrokeColor: config.themes.majorTickStrokeColor(props),
        valueMarkerFillColor: config.themes.valueMarkerFillColor(props),
        valueMarkerLabelFillColor: config.themes.valueMarkerLabelFillColor(props),
    };

    const computedProps = { dataSources, options: null, ...options, ...defaultColors };
    return computedProps;
};

const MarkerGauge = withDashboardViz({
    ReactViz: ReactMarkerGauge,
    computeVizProps,
    vizConfig: config,
});

export default MarkerGauge;

export { config };
